.zsku-panel {
	padding: 2em;
	.zsku-panelHeading {
		position: relative;
		display: flex;
		gap: 1em;
		align-items: baseline;
		width: 100%;
		h1 {
			margin: 0 0 0.5em 1em;
			font-size: 1em;
			font-weight: bold;
			color: #99a;
			text-transform: uppercase;
			letter-spacing: 0.15em;
		}
		.logo {
			position: absolute;
			right: 3%;
			top: 0;
			width: 5em;
			text-align: center;
			filter: drop-shadow(1px 1px 3px rgba(0, 0, 0, 0.25));
			svg {
				margin-top: -4em;
				border-radius: 50%;// circle(2.5em);
				color: white;
				padding: 1em;
				width: 4em;
			}
		}
	}

	.headerInput {
		margin-top: 1em;
		display: flex;
		align-items: center;
		gap: 2em;
	}
}

.zsku-keyValueGrid {
	padding: 1em;
	display: grid;
	grid-template-columns: min-content auto;
	row-gap: 1em;
	column-gap: 3em;
	align-items: center;
	max-width: 800px;

	h4 {
		margin: 0;
		color: rgb(78, 78, 87);
		white-space: nowrap;
	}
	p {
		margin: 0;
	}
}

.sidebar {
	height: 100vh;
	border-top: 15px solid #ccc;
	box-sizing: border-box;
	box-shadow: 0px 0px 10px 1px rgba(0, 0, 0, 0.25);
	background-color: #fff;
	position: relative;
	min-width: 300px;
	&.minimized {
		min-width: 1em;
		border-top: none;
		a li {
			h4 {
				display: none;
			}
			margin: 0;
			padding: .75em;
		}
		.sidebarUser {
			h1,
			p {
				display: none;
			}
			margin: 0;
			padding: 1em 0.25em;
			display: block;
			border-bottom: none;
		}
	}

	.sidebarUser {
		h1 {
			margin: 0.75em 0 0 0;
		}
		p {
			margin: 0;
			color: #777;
			font-size: 1em;
		}
		display: flex;
		align-items: flex-end;
		gap: 1em;
		padding: 1.5em;
		padding-right: 0em;
		border-bottom: 1px solid #bbb;
	}

	ul {
		list-style: none;
		padding-left: 0;
		a {
			text-decoration: none;
			color: black;
			li {
				padding: 0.75em;
				display: flex;
				border-radius: 0.3em;
				align-items: center;
				font-weight: bold;
				margin: 0.5em 0.75em;
				font-size: 1em;
				h4 {
					padding-left: 0.5em;
					margin: 0;
				}
				&:hover {
					background-color: rgb(218, 230, 240);
					h4,
					svg {
						color: rgb(23, 57, 107);
					}
				}
			}
		}
	}

	.minimize {
		position: absolute;
		bottom: 0;
		right: 0;
	}
}

.landingMain {
	width: 100%;
	height: 100%;
	justify-content: center;
	align-items: center;
	display: flex;
	text-align: center;
	p,
	h1 {
		margin: 0;
	}
}
