.mainContainer {
	display: flex;

	.contentContainer {
		width: 100%;
		height: 100vh;
		max-height: 100vh;
		overflow-y: auto;
	}
}

.cardGrid {
	display: grid;
	padding: 0;
	// max-width: 1000px;
	// grid-gap: 2em 2em;
	grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
	.card {
		&:hover {
			transform: scale(1.01);
			transition-duration: 200ms;
		}
		cursor: pointer;
	}
}

a {
	text-decoration: none;
	color: #2978bf;
	font-weight: bold;
	&:visited{
		color: #2978bf;
	}
}